.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Exchange {
  background-color: #f6f6f6;
  width: 30%;
  min-width: 300px;
  min-height: 500px;
  color: #0a93e2;
  border-radius: 20px 20px 20px 20px;
  padding: 30px;
}
.Exchange h3{
  color: #282c34;
  text-align: left;
  margin-bottom: 0;
}
.Exchange p{
  text-align: left;
  margin-top: 0;
  font-size: calc(10px + 1vmin);
}

.ExchangeBlock {
  background-color: #FFFFFF;
  width: 90%;
  height: 62px;
  color: #282c34;
  border-radius: 10px 10px 10px 10px;
  padding: 10px 20px;
  text-align: start;
  margin: 0;
}
.ExchangeBlock * {
  border: 0;
  outline: none !important;
}
.ExchangeBlockText {
  color: #282c34;
  font-size: calc(10px + 2vmin);
  height: 35px;
  text-align: right;
  width: 80%;
  border: 0;
}
.ExchangeBlockText:focus,
.ExchangeBlockExchangeBlockText:focus {
  border: 0;
  outline: none !important;
}

button.ExchangeSubmit{
  background-color: #03548d;
  height: 50px;
  color: #ffffff;
  border-radius: 20px 20px 20px 20px;
  border: 1px solid #03548d;
  width: 60%;
  min-width: 200px;
}
button.ExchangeSubmit:disabled{
  opacity: 0.3;
}

.BalanceText {
  font-size:  calc(10px + 1vmin);
  padding: 0;
  width: 60%;
  white-space: nowrap;
}
.ExchangeBody{
  height: 80%;
}
.SubmitNotification,
.ErrorNotification,
.NotificationSuccess {
  text-align: left;
  padding: 25px 5px;
  height: 100px;
  width: 100%;
  font-size: calc(8px + 1vmin);
  color: #d62424;
  display: flex;
}
.ErrorNotification{
  padding: 0;
  font-size: calc(8px + 1vmin);
  white-space: nowrap;
  width: auto;
}
.NotificationSuccess {
  color: #006600;
  white-space: pre;
  font-size: calc(10px + 2vmin);
}
select {
  color: #282c34;
  border: 0;
  font-size: calc(10px + 2vmin);
  outline: none !important;
}
.ExchangeBlockTextError {
  color: #282c34;
  font-size: calc(10px + 2vmin);
  height: 35px;
  text-align: right;
  width: 100%;
  border: 1px solid #d62424;
}
.ExchangeCombobox {
  width: auto;
}
